import React, { Component } from 'react'
import InputText from '@mui/material/TextField'
import Button from '@mui/material/Button'

class Login extends Component{
    constructor(props, context){
        super(props, context);
        this.state = {
            user:"",
            password:"",
            passwordConfirm:""
        }


    }

    render(){

        return(
            <div className="d-flex justify-content-center">
                <div className="w-50 h-50 align-self-center pt-5">
                    <h3 className="justify-content-center">Login</h3>
                    <InputText id="user" label="User" variant="filled" />
                    <InputText id="password" label="Password" variant="filled" />
                    <div className="d-flex">
                        <div className=" w-50">
                            <Button variant="contained">Login</Button>
                        </div>
                        <div className="w-50">
                            <Button variant="contained">SingUp</Button>
                        </div>
                    </div>
                </div>
            </div>
        )
    }

}

export default Login;